.header div nav a:nth-child(3) {
  color: #f16c75;
}

/* Sass / Css / Stylemain / 1600px */
a {
  text-decoration: none;
  list-style: none;
  color: black;
}

html {
  background-size: cover;
  background-repeat: no-repeat;
}

body {
  margin: 0;
  padding: 0;
  border: 0;
}

.header {
  display: flex;
  width: 100%;
  height: 80px;
}
.header div {
  display: flex;
  width: 90%;
  height: 100%;
  margin: 0 auto;
}
.header div h2 {
  width: 18%;
  margin: auto 0;
  font-weight: 700;
}
.header div nav {
  display: flex;
  width: 40%;
  margin: auto;
}
.header div nav a {
  margin: auto;
}
.header div nav h4 {
  margin: auto;
  cursor: pointer;
}
.header div nav h4:hover {
  color: #f7dd80;
  transition: 0.5s;
}
.header div div {
  width: 15%;
  height: auto;
  margin: 0;
}
.header div div a {
  display: block;
  display: flex;
  margin: auto 0;
  width: 35px;
  height: 35px;
  border-radius: 100%;
  cursor: pointer;
}
.header div div a:nth-child(2) {
  margin: auto;
}
.header div div a:hover {
  background-color: #f16c75;
  transition: 1s;
}
.header div div img {
  width: auto;
  height: 50%;
  margin: auto;
}
.header div div img:nth-child(3) {
  width: 60%;
  height: auto;
}

/* Prelude / button */
.prelude {
  width: 100%;
  height: 90vh;
  display: flex;
  /* prelude container */
}
.prelude div {
  display: flex;
  width: 90%;
  height: 100%;
  margin: 0 auto;
  /* container text */
}
.prelude div div {
  display: inline-block;
}
.prelude div div div {
  display: flex;
  width: 50%;
  height: 50px;
  margin: 14vh 0 0 0;
}
.prelude div div div span {
  margin: auto;
  font-weight: 600;
}
.prelude div div h1 {
  margin: 7vh 0 0 0;
}
.prelude div div p {
  margin: 4vh 0 0 0;
  width: 75%;
  word-spacing: 4px;
}
.prelude div div button {
  margin: 6vh 0 0 12%;
  position: absolute;
  display: block;
  width: 150px;
  height: 150px;
  border-radius: 100%;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transform: rotate(-15deg);
  font-size: 1.1vw;
  line-height: 1.5vw;
}
.prelude div div button:hover {
  background-color: #f16c75;
  transition: 1s;
}
.prelude div div img {
  width: 60%;
  margin: 13vh 0 0 40%;
}

/* events section */
.events {
  margin-top: 200px;
  display: inline-block;
  width: 100%;
}
.events h2 {
  text-align: center;
  width: 100%;
  height: 30%;
  margin: 0 0 15vh 0;
}
.events__container {
  display: flex;
  width: 90%;
  margin: 0 auto;
}

.events__band {
  display: flex;
  width: 60%;
  height: 550px;
}

.concerto {
  background-image: url(/images/band\ image.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 95%;
  margin: 0;
  border-radius: 7px;
}
.concerto span {
  display: block;
  width: 12%;
  height: 30px;
  font-size: 15px;
  background: rgba(255, 255, 255, 0.5);
  color: white;
  margin: 25px 0 0 30px;
  border-radius: 2px;
  text-align: center;
}
.concerto p {
  color: white;
  font-weight: 400;
  font-size: 25px;
  margin: 270px 0 20px 30px;
}
.concerto h4 {
  margin: 0 0 0 30px;
  color: white;
}

.events__party {
  width: 40%;
}
.events__party div {
  width: 100%;
  height: 163px;
  margin: 0;
  border-radius: 10px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(/images/festival\ 1\ image.jpg);
  color: white;
}
.events__party div:nth-child(2) {
  margin: 30px 0 31px 0;
  background-image: url(/images/festival\ 2\ image.jpg);
}
.events__party div:nth-child(3) {
  background-image: url(/images/festival\ 3\ image.jpg);
}
.events__party div h5 {
  display: flex;
  width: 92%;
  padding: 15px 20px 0 20px;
  margin: 0;
  font-size: 25px;
}
.events__party div h5 span {
  display: block;
  width: 20%;
  font-size: 15px;
  height: 30px;
  margin: auto 0 auto auto;
  text-align: center;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 2px;
}
.events__party div h5 span:hover {
  background: #1f1f1f;
  border: 2px solid white;
  border-radius: 5px;
  transition: 0.1s;
  cursor: pointer;
}
.events__party div p {
  padding: 25px 0 0 20px;
}

.events__category {
  display: flex;
  width: 100%;
  margin-top: 5.5vh;
}
.events__category div {
  display: flex;
  width: 90%;
  margin: 0 auto;
  color: white;
}
.events__category div span {
  display: block;
  width: 48.5%;
  height: 330px;
  margin: 0 auto 0 0;
  background-image: url(/images/category\ image\ 1.jpg);
  border-radius: 10px;
  background-size: cover;
  background-repeat: no-repeat;
}
.events__category div span span {
  display: block;
  text-align: center;
  border-radius: 2px;
  background-image: none;
  height: 30px;
  width: 80px;
  margin: 30px 0 0 30px;
  background: rgba(255, 255, 255, 0.5);
}
.events__category div span p {
  margin: 80px 0 0 30px;
  font-size: 28px;
}
.events__category div span h5 {
  margin: 30px 0 0 30px;
  font-size: 47px;
  line-height: 50px;
}
.events__category div span:nth-child(2) {
  margin: 0 0 0 auto;
  background-image: url(/images/category\ image\ 2.jpg);
}

.tickets {
  display: flex;
  width: 100%;
  margin: 15vh auto;
}

.tickets__container {
  display: flex;
  width: 90%;
  height: 400px;
  margin: 0 auto;
  border-radius: 8px;
  background-color: #F3F3F3;
}

.add__tickets {
  display: flex;
  width: 55%;
  height: 100%;
}
.add__tickets h3 {
  display: flex;
  width: 70%;
  height: 40px;
  font-size: 32px;
  margin: 20px 0 0 50px;
}

.tickets__category {
  width: 50%;
}
.tickets__category span {
  display: block;
  margin: auto 0 auto 50px;
  font-size: 15px;
  color: #969696;
}
.tickets__category span:nth-child(2) {
  margin-top: 55px;
}
.tickets__category span:nth-child(3) {
  margin-top: 35px;
}
.tickets__category span:nth-child(4) {
  margin-top: 30px;
}
.tickets__category span p {
  margin: 0;
  font-size: 22px;
  color: black;
}

.minus {
  width: 30px;
  height: 30px;
  margin: auto 10px auto auto;
  cursor: pointer;
}

.plus {
  width: 30px;
  height: 30px;
  margin: auto auto auto 10px;
  cursor: pointer;
}

.tickets__add {
  width: 50%;
}
.tickets__add h4 {
  width: 40%;
  height: 30px;
  text-align: right;
  margin: 27px auto auto auto;
  font-weight: 500;
}
.tickets__add span {
  width: 50%;
  margin: 60px auto;
  display: flex;
}
.tickets__add span p {
  width: 10%;
  margin: auto;
  text-align: center;
  font-size: 25px;
  font-weight: bold;
}

.add__payment {
  display: flex;
  width: 45%;
  height: 100%;
}
.add__payment form {
  display: inline-block;
  width: 90%;
  margin: 25px auto 0 auto;
}
.add__payment form input {
  width: 91%;
  margin: 10px auto;
}
.add__payment form input:nth-child(1) {
  margin-top: 0;
}
.add__payment form input:nth-child(2) {
  width: 40.5%;
}
.add__payment form input:nth-child(2):hover {
  width: 45.5%;
}
.add__payment form input:nth-child(3) {
  width: 40.5%;
  margin-left: 4%;
}
.add__payment form input:nth-child(3):hover {
  width: 45.5%;
}
.add__payment form button {
  margin-top: 10px;
}

footer {
  display: flex;
  width: 100%;
  height: 40px;
  background-color: #F3F3F3;
}
footer span {
  display: flex;
  margin: auto;
  font-size: 15px;
}
footer span img {
  width: 50%;
  height: 15px;
  margin: 5px auto auto 10px;
  background-size: contain;
}

